.recommended {
  padding-block-start: 64px;

  @include onTablet {
    padding-block-start: 120px;
  }

  &__title {
    margin-bottom: 48px;

    @include onTablet {
      margin-bottom: 56px;
    }
  }

  &__products {
    display: flex;
    flex-direction: column;
    gap: 40px;

    @include onTablet {
      gap: 56px;
    }

    @include onDesktop {
      @include pageGrid;
    }
  }

  &__product {
    display: flex;
    flex-direction: column;

    @include onDesktop {
      grid-column: span 4;
    }

    &-photo {
      width: 100%;
      margin-bottom: 24px;
      border-radius: 4px;
      @include hover(transform, scale(1.03));
    }

    &-title {
      font-family: "Space Grotesk", sans-serif;
      margin-bottom: 8px;
      font-size: 18px;
      font-weight: 700;
      line-height: 140%
    }

    &-description {
      margin-bottom: 8px;

      @include onDesktop {
        max-width: 280px;
        min-height: 112px;
      }

      @media (min-width: 1131px) {
        min-height: 88px;
      }

      & span {
        @include onDesktop {
          display: none;
        }
      }
    }

    &-price {
      font-family: "Space Grotesk", sans-serif;
      font-size: 16px;
      font-weight: 700;
      line-height: 140%;
    }
  }
}