.about-us {
  padding-block-start: 48px;
  display: flex;
  flex-direction: column;
  gap: 24px;

  @include onTablet {
    padding-block-start: 56px;
    @include pageGrid;
  }

  &__title {
    @include onTablet {
      grid-column: span 3;
    }

    @include onDesktop {
      grid-column: span 6;
    }
  }

  &__description {
    @include onTablet {
      grid-column: span 3;
    }

    @include onDesktop {
      grid-column: span 6;
    }
  }
}