.products {
  padding-block-start: 96px;

  @include onTablet {
    padding-block-start: 120px;
  }

  &__title {
    margin-bottom: 48px;

    @include onTablet {
      margin-bottom: 56px;
    }
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    gap: 40px;

    @include onTablet {
      gap: 56px;
    }
  }

  &__product {
    &-photos {
      @include pageGrid;
      margin-bottom: 16px;
    }

    &-link {
      position: relative;
      overflow: hidden;

      &--wide {
        @include onTablet {
          grid-column: span 4;
        }

        @include onDesktop {
          grid-column: span 8;
        }
      }

      &--square {
        height: 0;
        padding-bottom: 100%;

        @include onTablet {
          grid-column: span 2;
        }

        @include onDesktop {
          grid-column: span 4;
        }
      }
    }

    &-photo {
      position: absolute;
      width: 100%;
      height: 100%;
      object-fit: cover;

      @include hover(transform, scale(1.1));
    }

    &-title {
      font-family: "Space Grotesk", sans-serif;
      font-size: 18px;
      font-weight: 700;
      line-height: 140%;

      @include onTablet {
        font-size: 24px;
      }
    }
  }

  &__button-wrapper {
    @include onDesktop {
      @include pageGrid;
    }
  }

  &__button {
    grid-column: span 4;
  }
}