.header {
  background-image: url("../images/header/header-bg-image.png");
  background-size: cover;
  background-position: center;

  &__container {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__title {
    font-family: "Space Grotesk", sans-serif;
    font-size: 36px;
    font-weight: 700;
    line-height: 100%;
    padding-bottom: 40px;
    max-width: 280px;

    @include onTablet {
      font-size: 48px;
      padding-bottom: 65px;
      max-width: 432px;
    }

    @include onDesktop {
      font-size: 56px;
    }
  }
}